import StatModel from '../../models/statisticModel';

export default class AppView {
  constructor(data) {
    this.data = data;
  }

  async card(i) {
    const modelStat = new StatModel(this.data);
    const dataStat = await modelStat.getStatistic().then(obj => obj.clipViewCount[i]);
    // подключаем fontawesome
    const linkPattern = 'https://www.youtube.com/watch?v=';
    const fontawesome = document.createElement('link');
    const rel = 'stylesheet';
    const href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    const integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';
    fontawesome.rel = `${rel}`;
    fontawesome.href = `${href}`;
    fontawesome.integrity = `${integrity}`;
    fontawesome.setAttribute('crossorigin', 'anonymous');
    document.head.appendChild(fontawesome);
    // создаем плоскость карточки
    const cardBorder = document.createElement('div');
    cardBorder.classList.add('cardBorder');
    // имя клипа
    const links = document.createElement('a');
    const titles = document.createElement('div');
    const clipTitle = this.data.title[i];
    const clipID = this.data.id[i];
    links.setAttribute('href', `${linkPattern}${clipID}`);
    links.setAttribute('target', '_blank');
    links.innerHTML = clipTitle;
    // превьюшка клипа
    const previews = document.createElement('img');
    const clipPreview = this.data.img[i];
    previews.src = clipPreview;
    // автор
    const authorIcon = document.createElement('i');
    authorIcon.classList.add('fas', 'fa-portrait');
    const authors = document.createElement('span');
    authors.classList.add('author');
    const clipAuthor = this.data.channel[i];
    authors.innerHTML = clipAuthor;
    // просмотры
    const views = document.createElement('i');
    views.classList.add('fas', 'fa-eye');
    const ViewCount = document.createElement('span');
    ViewCount.classList.add('view');
    ViewCount.innerHTML = dataStat;
    // дата
    const publishDateIcon = document.createElement('i');
    publishDateIcon.classList.add('fas', 'fa-clock');
    const publishDate = document.createElement('span');
    publishDate.classList.add('publishDate');
    const clipPublishDate = new Date(this.data.datePublishing[i]);
    publishDate.innerHTML = clipPublishDate.toLocaleDateString();

    // описание
    const descript = document.createElement('p');
    descript.classList.add('decription');
    const clipDescription = this.data.decrip[i];
    descript.innerHTML = clipDescription;
    // вывод данных на сайт


    const slider = document.getElementsByClassName('slider')[0];
    slider.appendChild(cardBorder);
    cardBorder.appendChild(titles);
    titles.appendChild(links);
    cardBorder.appendChild(previews);
    authors.appendChild(authorIcon);
    cardBorder.appendChild(authors);
    publishDate.appendChild(publishDateIcon);
    cardBorder.appendChild(publishDate);
    ViewCount.appendChild(views);
    cardBorder.appendChild(ViewCount);
    cardBorder.appendChild(descript);
  }
}
