export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractClipInfo(data) {
    const clipPageToken = data.nextPageToken;
    const clipTitle = data.items.map(clip => clip.snippet.title);
    const clipId = data.items.map(clip => clip.id.videoId);
    const clipPreview = data.items.map(clip => clip.snippet.thumbnails.medium.url);
    const clipAuthor = data.items.map(clip => clip.snippet.channelTitle);
    const clipDescription = data.items.map(clip => clip.snippet.description);
    const clipDate = data.items.map(clip => clip.snippet.publishedAt);
    const cardContent = {
      nextPageToken: clipPageToken,
      title: clipTitle,
      id: clipId,
      img: clipPreview,
      channel: clipAuthor,
      decrip: clipDescription,
      datePublishing: clipDate,
    };
    return cardContent;
  }

  async getNextCard(data) {
    const url = `${this.state.url}&pageToken=${data.nextPageToken}`;
    const responce = await fetch(url);
    const newdata = await responce.json();
    return AppModel.extractClipInfo(newdata);
  }

  async getClipInfo() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractClipInfo(data);
  }
}
