export default class StatModel {
  constructor(data) {
    this.data = data;
    this.urlStat = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAv9DCXl-tLNcBb1I-G73GMR2SSALmErNk&id=${data.id}&part=snippet,statistics`;
  }

  static extractStatData(dataStat) {
    const clipViewCount = dataStat.items.map(clip => clip.statistics.viewCount);
    const obj = {
      clipViewCount,
    };
    return obj;
  }

  async getStatistic() {
    const url = this.urlStat;
    const responce = await fetch(url);
    const dataStat = await responce.json();
    return StatModel.extractStatData(dataStat);
  }
}
