import App from './controllers/App';
import sliderBeh from './controllers/sliderBeh';

const url = 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyAv9DCXl-tLNcBb1I-G73GMR2SSALmErNk&type=video&part=snippet&maxResults=15&q=';
const searchInput = document.createElement('input');
searchInput.type = 'text';
searchInput.className = 'search';
document.body.appendChild(searchInput);

const button = document.createElement('button');
document.body.appendChild(button);
button.className = 'btn-search';
button.innerHTML = 'Search';

const sliderContainer = document.createElement('div');
sliderContainer.className = 'slider';

const searchData = document.getElementsByTagName('input')[0];
const btn = document.getElementsByTagName('button')[0];
const searchBlock = document.createElement('div');
document.body.appendChild(searchBlock);
searchBlock.classList.add('searchBlock');
searchBlock.appendChild(searchData);
searchBlock.appendChild(btn);
document.body.appendChild(sliderContainer);

btn.addEventListener('click', () => {
  const newURL = url + searchData.value;

  const app = new App(newURL);
  app.start();
});
searchData.addEventListener('keydown', (event) => {
  if (event.keyCode === 13) {
    const newURL = url + searchData.value;

    const app = new App(newURL);
    app.start();
  }
});
sliderBeh();
