import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor(url) {
    this.state = {
      url,
    };
  }

  async start() {
    const model = new AppModel(this.state);
    const data = await model.getClipInfo();
    const view = new AppView(data);
    const clearSlider = document.getElementsByClassName('slider')[0];
    clearSlider.innerHTML = '';
    let l = 0;
    // eslint-disable-next-line no-restricted-syntax
    for (const key in data) {
      if (Object.prototype.hasOwnProperty.call(data, key)) {
        const element = data[key];
        l = element.length;
      }
    }
    for (let k = 0; k < l; k += 1) {
      view.card(k);
    }

    const buttonNext = document.createElement('button');
    buttonNext.className = 'btn-next';
    buttonNext.innerHTML = 'Next clips';
    clearSlider.appendChild(buttonNext);
    const nextBtn = document.getElementsByTagName('button')[1];
    nextBtn.onclick = async function nexpage() {
      const newdata = await model.getNextCard(data);
      const view1 = new AppView(newdata);
      // eslint-disable-next-line no-restricted-syntax
      for (const key in newdata) {
        if (Object.prototype.hasOwnProperty.call(newdata, key)) {
          const element = newdata[key];
          l = element.length;
        }
      }
      for (let k = 0; k < l; k += 1) {
        view1.card(k);
      }
    };
  }
}
